/// <reference path="./module.d.ts" />
import React=require ("../node_modules/ReactTS/lib/ReactTS")
//todo переписать view port так чтобы он работал как rendercomponent


class ViewPort extends React.BaseComponent {
    //TODO некорректно возвращает высоту
    getClientHeight(){
        var height=document.compatMode=='CSS1Compat' && !window["opera"]?document.documentElement.clientHeight:document.body.clientHeight;
        return height;
    }

    componentWillMount() {
        window.addEventListener("resize", function(){
            this.forceUpdate();
        }.bind(this));    
    }


    //render
    render() {
	return React.DOM.div({
            style:{
                width:"100%",
	        height:""+this.getClientHeight()+"px",
                display:"flex"
            }
        },this.props.children);
    }
}

React.registerModule(module);
export=ViewPort;


